<template>
  <div id="partTime">
    <van-cell class="ptCell" title="" value="2" />
    <van-cell class="ptCell" title="" :value="classT" @click="showPopup0" />
    <van-cell class="ptCell" title="" :value="classN" @click="showPopup2" />
    <!-- <van-cell class="ptCell" title="" :value="ptN" @click="showPopup3" /> -->
    <!-- <van-field class="ptCell" v-model="value" label="" placeholder="10" input-align="right" type="digit"/> -->
    <van-field
      class="ptCell"
      v-model="message"
      rows="4"
      height="100px"
      label=""
      type="textarea"
    />
    <van-row type="flex" justify="center">
      <van-col span="23">
        <van-button class="maBu" color="#209aff" size="large"></van-button>
      </van-col>
    </van-row>
    <!--  -->
    <van-popup v-model="show0" position="bottom" :style="{ height: '35%'}" round>
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="confirmTime()"
      />
    </van-popup>
    <!--  -->
    <van-popup v-model="show1" position="bottom" :style="{ height: '35%'}" round>
      <van-picker
        show-toolbar
        title
        :columns="columns"
        @cancel="show1=false"
        @confirm="confirmClass"
      />
    </van-popup>
    <!--  -->
    <van-popup v-model="show2" position="bottom" :style="{ height: '35%'}" round>
      <van-picker
        show-toolbar
        title
        :columns="columns1"
        @cancel="show1=false"
        @confirm="confirmName"
      />
    </van-popup>
  </div>
</template>

<script>
import Vue from "vue";
import {
  Picker,
  Button,
  Cell,
  CellGroup,
  Field,
  DatetimePicker,
  Popup,
  Row,
  Col
} from "vant";
Vue.use(Picker)
  .use(Button)
  .use(Cell)
  .use(Field)
  .use(Row)
  .use(Col)
  .use(DatetimePicker)
  .use(Popup)
  .use(CellGroup);
export default {
  name: "partTime",
  data() {
    return {
      value: "",
      message: "",
      show0: false,
      show1: false,
      show2: false,
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(),
      classT: "",
      classN: "",
      ptN: "",
      columns: ["", "", "", "", ""],
      columns1: ["", "", "", "", ""]
    };
  },
  methods: {
    showPopup0() {
      this.show0 = true;
    },
    showPopup2() {
      this.show1 = true;
    },
    showPopup3() {
      this.show2 = true;
    },
    //
    confirmTime() {
      //
      this.time =
        this.currentDate.getFullYear() +
        "-" +
        (Number(this.currentDate.getMonth()) + 1) +
        "-" +
        this.currentDate.getDate();
      //
      this.time1 = new Date(this.currentDate).getTime() / 1000;
      this.show0 = false;
      window.console.log(this.time);
      this.classT = this.time;
    },
    confirmClass(e) {
      window.console.log(e);
      this.classN = e;
      this.show1 = false;
    },
    confirmName(e) {
      window.console.log(e);
      this.ptN = e;
      this.show2 = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.ptCell {
  margin-bottom: 5px;
}
</style>
